<template>
  <div>
      <van-nav-bar
      title="日历"
      left-text="返回"
      style="height: 46px"
      left-arrow
      @click-left="$router.go(-1)"
    />
      <!-- 日历 -->
     <van-calendar title="当前日期" ref="calendar" :min-date="minDate" :max-date="maxDate" :poppable="false" :show-confirm="false" :style="{ height: '600px' }"/>
  </div>
</template>

<script>
export default {
    data() {
        return {
            minDate: new Date(new Date().getFullYear()-30, 0, 1),
            maxDate: new Date(new Date().getFullYear()+30, 12, 31),
        }
    },
    methods: {
        goScrollToDate() {
            this.$refs.calendar.scrollToDate(new Date());
        }
    }
}
</script>

<style>

</style>


